@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

tbody {
    margin: 20px;
}

.datatable .col {
    cursor: pointer;
  }
  
  .datatable .col .pointer:after {
    font-size: 80%;
    content: "";
    margin-right: 18px;
  }
  
  .datatable .col .pointer:hover:after {
    font-size: 80%;
    margin-right: 0;
  }
.searchbox {
    padding-bottom: 10px;
    margin-bottom: 10px;
}
.searchbox {
    padding: 20px;
}
.data-area {
    margin: 0 auto;
    width: 90%;
  }
.Container {
    width: 100%;
    height: 100%;
    font-family: 'Comfortaa', cursive;
    font-weight: bolder;
}

.header {
    padding: 30px 0;
    color: white;
    background-color: #3f3e3e;
    width: 100%;
    text-align: center;
    font-family: 'Comfortaa', cursive;
    border-bottom: 5px solid rgb(250, 201, 201);
    border-top: 5px solid rgb(247, 183, 183);
    margin-bottom: 10px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

